.modal {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    height: 100vh;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0;
    pointer-events: none;
    transition: 0.5s;
    z-index: 100;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.modal_content {
    width: 410px;
    border-radius: 12px;
    background-color: white;
    transform: scale(0.5);
    transition: 0.4s all;
    position: relative;
    padding: 30px;
    box-shadow: 0 0 8px rgba(0, 0, 0, 0.1);
    font-family: 'Poppins', sans-serif;
    max-height: 90vh;
}

.modal_content.active {
    transform: scale(1);
}

.close {
    position: absolute;
    right: 30px;
    cursor: pointer;
    font-size: 20px;
    color: #ff7400;
}

@media (max-width: 468px) {
    .modal_content {
        width: 320px;
    }
}
